#mobile-button {
    display: block;
    height: auto;
    position: absolute;
    z-index: 999;
    cursor: pointer;
    padding: 5px 5px 3px 5px;
    margin: 10px;
    border-radius: 50%;
    background: #272626;
    transition: all 0.3s ease-in-out;
    -webkit-box-shadow: 3px 3px 6px -2px rgba(0,0,0,0.62);
-moz-box-shadow: 3px 3px 6px -2px rgba(0,0,0,0.62);
box-shadow: 3px 3px 6px -2px rgba(0,0,0,0.62);

    @include media(800px) {
      display: none;
    }
}

.mobile__menu {
    display: inline-block;
    margin: 0;
    border-radius: 50%;
  border: 4px solid #fbb040;
  padding: 11px 10px 9px 10px;
  width: 46px;
  height: 46px;
  transition: all 0.3s ease-in-out;

}

.mobile__menu-bar {
    background-color: #fff;
    border-radius: 1px;
    display: block;
    width: 16px;
    height: 3px;
    margin: 0 auto 3px;
}
