.navigation__primary {
  color: #fff;
  text-transform: uppercase;
  list-style: none;
  padding-right: 40px;
  border-top: 1px solid #646363;
  border-bottom: 1px solid #646363;
  padding: 20px;
  margin: 0 30px;
}

.navigation__item {
  text-align: center;

  & a {
    color: #fff;
    font-weight: 300;
    letter-spacing: 2px;
    text-decoration: none;
    padding: 10px 0;
    font-size: 12px;
    display: inline-block;
    &:hover {
      color: #fbb040;
    }
  }
}

.navigation__submenu {
  list-style: none;
  margin: 0;
  padding: 15px 0;
}

.navigation__subitem {
  text-align: center;
  & a {
    color: #fff;
    text-decoration: none;
    letter-spacing: 2px;
    display: inline-block;
    font-size: 10px;
    text-transform: initial;
    padding: 10px 0;
    &:hover {
      color: #fbb040;
    }
  }
}

.logo {
  margin-bottom: 30px;
}
