#sidebar {
  background: url('../images/cover.png') 0 0 transparent repeat;
    position: fixed;
    background-color: #272626;
    top: 0;
    left: 0;
    width: 260px;
    z-index: 101;
    height: 100%;
    padding-top: 53px;
    color: #fff;
    text-align: center;
    -webkit-box-shadow: 3px 3px 6px -2px rgba(0,0,0,0.62);
-moz-box-shadow: 3px 3px 6px -2px rgba(0,0,0,0.62);
box-shadow: 3px 3px 6px -2px rgba(0,0,0,0.62);
    @include media(800px) {
    }
    h1 {
      font-size: 18px;
      font-weight: 300;
      text-transform: uppercase;
    }
    h2 {
      font-size: 14px;
      font-weight: 300;
      text-transform: uppercase;
    }
}

.headline__rules {
  position: relative;
  display: block;
  border-top: 1px solid #646363;
  // border-bottom: 1px solid #fff;
  margin: 20px 30px;
padding: 10px;


  &::before {
    top: 30px;
    border-top: 1px solid #fff;
  }
  &::after {
    bottom: 10px;
    border-bottom: 1px solid #fff;
  }
}

#content {
    position: relative;
    // z-index: 100;
    background: #f5f5f5;
    // display: flex;
    z-index: 102;
    transition: transform 0.33s cubic-bezier(0.33, 0, 0.2, 1) 0s;
    -webkit-box-shadow: inset 3px 0px 6px -2px rgba(0,0,0,0.56);
  -moz-box-shadow: inset 3px 0px 6px -2px rgba(0,0,0,0.56);
  box-shadow: inset 3px 0px 6px -2px rgba(0,0,0,0.56);
    @include media(800px) {
      margin-left: 260px;
  }
}

#ajax-content {
  display: none;
}

.container {
  flex-wrap: wrap;
  @include display(flex);
  @include flex-direction(row);
  @include justify-content(flex-start);

}

.wrapper {
  overflow: hidden;
}

.header__bottom {
  position: absolute;
  bottom: 0;
}
