.menu--is-open {
  #content {
    transform: translate3d(260px, 0, 0);
  }
  @include media(800px) {
    #content {
      transform: translate3d(0, 0, 0);
    }
  }
  & .mobile__menu {
    border: none;
  }
  & #mobile-button {
    margin: 0;
    border-radius: 0;
  }  
  & .mobile__menu-bar:nth-child(1) {
    transform: rotate(45deg) scale(1.5);
    position: relative;
     top: 12px;
    transition: all 0.3s ease-in-out;
  }
  & .mobile__menu-bar:nth-child(2) {
    transform: rotate(-45deg) scale(1.5);
    position: relative;
     top: 6px;
    transition: all 0.3s ease-in-out;
  }
  & .mobile__menu-bar:nth-child(3) {
    display: none;
  }
}
