.portfolio__item {
  flex-grow: 1;
  width: 100%;
  height: auto;
  width: 50%;
  order: 1;
  overflow: hidden;
  background: #DFB227;
  @include media(1200px) {
    width: 33.33334%;
  }
  & h2 {
    position: absolute;
    right: 0;
    bottom: 0;
    padding: 1em 1.2em;
    font-family: 'Lato', Arial, sans-serif;
    text-transform: uppercase;
    color: #fff;
    font-weight: 300;
    font-size: 20px;
  }

  & span {
    position: absolute;
    top: 30px;
    padding: 0 10px 0 0;
    width: 50%;
    border-right: 1px solid #fff;
    text-align: right;
    opacity: 0;
    transition: opacity 0.35s, transform 0.35s;
    transform: translate3d(-40px,0,0);
    color: #fff;
  }
  & figcaption {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.portfolio__image {
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;

  &:hover {
    & img {
	    opacity: 0.5;
  	  transform: translate3d(0,0,0) scale(1);
    }
    & span {
      opacity: 1;
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
    }
  }


  & img {
  	width: calc(100% + 60px);
  	opacity: 1;
  	transition: opacity 0.35s, transform 0.35s;
  	transform: translate3d(-30px,0,0) scale(1.12);
  }
}
